/** 
 * 对日期进行格式化， 
 * @param date 要格式化的日期 
 * @param format 进行格式化的模式字符串
 *     支持的模式字母有： 
 *     y:年, 
 *     M:年中的月份(1-12), 
 *     d:月份中的天(1-31), 
 *     h:小时(0-23), 
 *     m:分(0-59), 
 *     s:秒(0-59), 
 *     S:毫秒(0-999),
 *     q:季度(1-4)
 *     W:周几
 * @return String
 * @author yanis.wang@gmail.com
 */
import Vue from 'vue' 
import {dateFormat} from '../utils/date'
Vue.filter('dateFormat', dateFormat);





